import {SET_SPORE_TOKEN_ID} from 'constants/action-types.js';
import {SET_SOLD_PLAYERS} from 'constants/action-types.js';

export const sporeReducer = (state = {}, action) => {
    let newState;
    switch (action.type) {
    case SET_SPORE_TOKEN_ID:
        return {
            ... state,
            tokenId: action.payload
        };
    case SET_SOLD_PLAYERS:
        return {
            ... state,
            soldPlayers: action.payload
        };
    default:
        return state;
    }
}
 
