import React, { useRef, useEffect, useCallback, useState} from 'react';
import {connect} from 'react-redux';
import {KEYON_SONGS} from './History.js';

const getKeyonBPM = async (url) => {
    let song = (await KEYON_SONGS()).find(x => x.metadata.url === url);
    if (!song) {
        return 0;
    }
    return song.bpm;
};

const JackerPlayer = ({jackedAudio}) => { 
    let [loaded, setLoaded] = useState();
    let ref = useRef();
    //let url = "https://zequencer.mypinata.cloud/ipfs/QmTo33Myv8gLHAZjXJQMvYtyS6P6rYf1MwzfBtrQ6s6jpd/?tokenId=40&gallery=true";
    //let url = "https://zequencer.mypinata.cloud/ipfs/QmVXsf4n7NET1YJ5tvueReKUkLMPW13ULoLbMZKTvbevNT/?tokenId=40&gallery=true";

    // last good one:
    //let url = "https://zequencer.mypinata.cloud/ipfs/QmafvKk7i8Uupdc8gNoD5VqQaEwdrafd9AaA1k4fJ9jgVe/?tokenId=40&gallery=true";

    // colorful waveform:
    //let url = "https://zequencer.mypinata.cloud/ipfs/QmQrGYEhHDdPdADaS9b5Lo56nUJENmTkBTNkS49m4wENjN/?tokenId=40&gallery=true";
    //let url = "https://zequencer.mypinata.cloud/ipfs/QmU6rmmjWWXCZd3NHjeu6pUVjBDZiMXuRx6EVtEocqz9dJ/?tokenId=40&gallery=true";

    // louder glow 
    let url = "https://zequencer.mypinata.cloud/ipfs/QmcdRjADLNki35XCkeonAzPcHoKnjRHpEQkrq3mx2a8GpA/?tokenId=40&gallery=true";

    useEffect(() => {
        window.addEventListener("message", onReceive, false);
    }, [setLoaded]);

    const onReceive = useCallback((e) => {
        if (e.origin !== "https://zequencer.mypinata.cloud") {
            return;
        }
        if (e.data === "ready for stems") {
            console.log("READY FOR STEMS");
            setLoaded(true);
        }
    }, [setLoaded]);

    useEffect(() => {
        console.log('USEFFECT jacked audio vs loaded', jackedAudio, loaded);
        if (!jackedAudio || !loaded) {
            return;
        }
        console.log("&&&&&&&&&&&&&&&& sending jacked audio=", jackedAudio);
        getKeyonBPM(jackedAudio).then(
            bpm => 
            ref.current.contentWindow.postMessage({
                type: "nft-jacker",
                body: {
                    bpm,
                    samples: {
                        drums: jackedAudio,
                        vocals: jackedAudio,
                        bass: jackedAudio,
                        other: jackedAudio
                    }
                }
            }, "*")
        );
    }, [jackedAudio, loaded]);

    useEffect(() => {
        if (loaded) {
            return;
            ref.current.contentWindow.postMessage({
                type: "nft-jacker",
                body: {
                    bpm: 128,
                    samples: {
                        drums: "QmbtStnnXjF5SY3v65C8D4JkHVU6zTVwPjLPF54pDRHXru",
                        vocals: "QmVJwnKYyL4gsVE8ENt7SL9M3Zasx5mqTRrVSSchrqYs2E",
 
                        other: "Qmev5j7vb6Ai2Yvmoy86koyv5G7fHaKNZPbMu77nkKgs3t",
                        
                    }
                }
            }, "*");
        }
    }, [loaded]);

    return (
        <iframe
          className={(loaded && jackedAudio) ? "loaded player" : "player"}
          width={340}
          height={400}
          ref={ref}
          src={url}
          
        />);
};

const mapStateToProps = (state, props) => {
  return {
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(JackerPlayer);
